function SkillSlideList(props){
	let {id, title, path, desc}=props.propsValue;

	return(
		<>
			<div className="skill">
				<strong>{title}</strong>
				<span className="line"></span>
			</div>
			<div className="icon_wrap">
				<ul>
					{
						path.map((d, i) => <li key={i}><img src={process.env.PUBLIC_URL + "/assets/images/" + d.img} alt={d.alt} /></li>)
					}
				</ul>
			</div>
			<div className="desc">
				<p>
					{
						desc.map((d, i) => <span key={i}>{d}</span>)
					}
				</p>
			</div>
		</>

	);
}

export default SkillSlideList;