import Title from "./Title";
import SubProjectsList from "./SubProjectsList";
import { useSelector } from "react-redux";

function SubProjects(){
	let title=useSelector(state => state.title);
	let subProjectsList=useSelector(state => state.subProjectsList);
	let data=subProjectsList;

	return(
		<section id="sub_section1">
			<div className="inner">
				<Title propsValue={title[3]}/>
				<div className="list" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="1000">
					<ul>
						{
							data.map((d, i) => <SubProjectsList key={i} propsValue={d}/>)
							
						}
					</ul>
				</div>
			</div>
		</section>
	);
}

export default SubProjects;