function SubSlideList(props){
	let {strong, span, cont, path, alt , link}=props.propsValue;

	return(
		<>
			<div className="desc">
				<strong>{strong}</strong>
				<span>{span}</span>
				<div className="icon_wrap">
					<a href="">
						<img src={process.env.PUBLIC_URL + "/assets/images/" + path} alt={alt}/>
					</a>
				</div>
				<p>
					{
						cont.map((d, i) => <span key={i}>{d}</span>)
					}
				</p>
			</div>
			<div className="link_wrap">
				<ul>
					<li><a href={link.linkHref} target="pop" className="link"><span>URL</span></a></li>
				{(link.gitHref ) ? <li><a href={link.gitHref} target="pop" className="link_more"><span>GitHub</span></a></li> : ""}	
				</ul>
			</div>
		</>

	);
}

export default SubSlideList;