import Title from "./Title";
import SubSlide from "./SubSlide";
import { useSelector } from "react-redux";

function SubOpenSource(){
	let title=useSelector(state => state.title);
	let subSlide=useSelector(state => state.subSlide);

	return(
		<section id="sub_section2">
			<div className="inner">
				<Title propsValue={title[4]}/>
				<SubSlide propsValue={subSlide}/>
			</div>
		</section>
	);
}

export default SubOpenSource;