function SubProjectsList(props){
	let {span, strong, desc, href, more}=props.propsValue;
	return(
		<li>
			<div className="content">
				<div className="left_box">
					<span>{span}</span>
					<strong>{strong}</strong>
				</div>
				<div className="center_box">
					<p>
						{
							desc.map((d, i) => <span key={i}>{d}</span>)
						}
					</p>
				</div>
				<div className="right_box">
					<a href={href} target="pop" className="site_more"><span>{more}</span></a>
				</div>
			</div>
		</li>
	);
}

export default SubProjectsList;