import Title from "./Title";
import SubCaseStudyList from "./SubCaseStudyList";
import { useSelector } from "react-redux";

function SubCaseStudy(){
	let title=useSelector(state => state.title);
	let caseList=useSelector(state => state.caseList);
	let data=caseList;
	return(
		<section id="sub_section3">
			<div className="inner">
				<Title propsValue={title[5]}/>
				<div className="list" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="1000">
					<ul>
						{
							data.map((d, i) => <SubCaseStudyList key={i} propsValue={d}/>)
						}
						
					</ul>
				</div>
			</div>
		</section>
	);
}

export default SubCaseStudy;