import { Swiper, SwiperSlide } from 'swiper/react';
import { Scrollbar } from 'swiper/modules';
import SkillSlideList from './SkillSlideList';
import 'swiper/css/scrollbar';
import 'swiper/css';

function SkillSlide(props){
	let data=props.propsValue;

	return(
		<div className="skill_slider">
			<Swiper
				scrollbar={{
					draggable: true,
					hide: false
				}}
				modules={[Scrollbar]}

				slidesPerView={1}
				spaceBetween={40}
				centeredSlides={true}
				breakpoints={{
					600: {	
					slidesPerView: 1.2,
					initialSlide: 1,
					centeredSlides: false,
						
					},
					797: {
					slidesPerView: 1.2,
					centeredSlides: false,
					},

					985: { 
						slidesPerView: 2.2,
						centeredSlides: false,
					},
				}}
				className="swiper-container mainSwiper"
			>
				{
					data.map((d, i) => <SwiperSlide key={i} ><SkillSlideList propsValue={d}/></SwiperSlide>)
					
				}
			</Swiper>
		</div>	
	);
}

export default SkillSlide;