function ContentText(props){
	let {top, middle, bottom}=props.propsValue;
	
	return(
		<div className="text_box">
			<div className="inner">
				<span className={top.className}><em>{top.label}</em></span>
				<span className={middle.className}>{middle.label}</span>
				<span className={bottom.className}>{bottom.label}</span>
			</div>
		</div>
	);
}
export default ContentText;