import Title from "./Title";
import SkillSlide from "./SkillSlide";
import { useSelector } from "react-redux";

function Skill(){
	let title=useSelector(state => state.title);
	let skillSlide=useSelector(state => state.skillSlide);
	return(
		<section id="section2">
			<div className="inner">
				<Title propsValue={title[1]}/>
				<SkillSlide propsValue={skillSlide}/>
			</div>
		</section>
	);
}

export default Skill;