import AboutTextTop from "./AboutTextTop";
import AboutTextBottom from "./AboutTextBottom";

function AboutText(props){
	let {top, bottom}=props.propsValue;

	return(
		<div className="text_box">
			<div className="right_box">
				<AboutTextTop propsValue={top}/>
				<AboutTextBottom propsValue={bottom}/>
			</div>
		</div>
	);
}

export default AboutText;