function SubCaseStudyList(props){
	let {span, strong, href, more}=props.propsValue;

	return(
		<li>
			<div className="content">
				<div className="left_box">
					<span>{span}</span>
					<strong>{strong}</strong>
				</div>
				<div className="right_box">
					<a href={href} target="pop" className="site_more"><span>{more}</span></a>
				</div>
			</div>			
		</li>
	);
}

export default SubCaseStudyList;