import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import gsap from "gsap";
import { isMobile } from "react-device-detect";
import { TweenMax, Linear } from "gsap/gsap-core";
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollTrigger from '@terwanerik/scrolltrigger';
import AOS from 'aos';
import Navigation from "./Navigation";
import 'aos/dist/aos.css';

function Header(props){
	let location=useLocation();
	let link=location.state;

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		let gnb=document.getElementById("gnb");
		let gnbLi=gnb.firstElementChild.children;
		let mobileGnb=document.getElementById("mobile");
		let mGnbLi=mobileGnb.firstElementChild.children;
		let page=document.querySelectorAll("section");
		let pageList=[];
		pageList[0]=document.getElementById("main");
		let headerFix=document.getElementById("header");
		let tab=document.getElementById("tab");
		let sitemap=document.getElementById("sitemap");
		let cursor=document.querySelector(".cursor");
		let rightMove=document.getElementById("right_move");
		let resume=document.querySelector(".resume");
		let textBox=document.querySelector(".text_box");
		let nameBox=document.querySelector(".name_box");
		let video=document.getElementById("main_video");

		let n=0;
		let winh;
		let topPos=0;

		for(let i=0; i<page.length; i++){
			pageList.push(page[i]);
		}
		function init(){
			if(link){
				let targety=pageList[Number(link)].offsetTop;

				gsap.to(window, {scrollTo: targety, duration: 0.5});
			}
			else{
				gsap.to(window, {scrollTo: 0, duration: 0.5});
			}
		}

		init();

		/* page color */
		function resizeHandler(){
			winh=window.innerHeight;

			if(isMobile){
				if(window.innerWidth > winh){ // landscape
					resume.style.display="none";
					textBox.classList.add("active");
					nameBox.classList.add("active");
				}
				else{
					resume.style.display="block";
					textBox.classList.remove("active");
					nameBox.classList.remove("active");
				}
			}
		}

		resizeHandler();

		window.addEventListener("resize", resizeHandler);

			
		/* 메인 스킬 글씨 서서히 나타나게 하기 */
		rightMove.classList.add("active"); 

		/* video */
		video.setAttribute("src", process.env.PUBLIC_URL+"/assets/images/main.mp4");

		video.addEventListener("loadeddata", function(){
			video.muted=true;
			video.play();
		});

		video.addEventListener("ended", function(){
			video.play();
		});



		/* scrolltrigger */
		const trigger=new ScrollTrigger({
			trigger: {
				once: true,
				toggle: {
					class: {
						in: "active",
						out: "inactive"
					}
				},
				offset: {
					viewport: {
						y: 0.25
					}
				}
			}
		});

		trigger.add("#main, section");

		gnbLi[n].firstElementChild.classList.add("on"); 

		window.addEventListener("scroll", scrollInteraction);

		function scrollInteraction(){
			let t=window.scrollY;

				if(t < pageList[1].offsetTop){
					n=0;
				}
				else if(t < pageList[2].offsetTop){
					n=1;
				}
				else if(t < pageList[3].offsetTop){
					n=2;
				}
				else if(t < pageList[4].offsetTop){
					n=3;

					if(window.innerHeight + t === document.body.scrollHeight){
						n=4;
					}
				}
				else {
					n=4;
				}

			for(let i=0; i<gnbLi.length; i++){
				if(i === n){
					if(gnbLi[i].classList.contains("on") === false){
						gnbLi[i].classList.add("on");
					}
				}
				else{
					if(gnbLi[i].classList.contains("on") === true){
						gnbLi[i].classList.remove("on");
					}
				
				}
			}

			if(t > 100){
				if(headerFix.classList.contains("active") === false){
					headerFix.classList.add("active");
					
					gsap.fromTo(headerFix, {top: -100}, {top: 0,  delay: 0.5});
				}

			}
			else{
				if(headerFix.classList.contains("active") === true){
					headerFix.classList.remove("active");
					headerFix.removeAttribute("style");
				}
			}
			if(t < pageList[2].offsetTop-winh*4/5 || t > pageList[3].offsetTop-winh*1/5){
				if(document.body.classList.contains("white") === true){
					document.body.classList.remove("white");   
				}
				else{
					return;
				}
				
			}
			else{
				if(document.body.classList.contains("white") === false){
					document.body.classList.add("white");
				}
				else{
					return;
				}
			}
		}	

		scrollInteraction();

		
		/* 마우스 커서 */
		if(isMobile){
			cursor.style.display="none";
		}
		else{
			document.body.addEventListener("mouseover", function(e){
				gsap.to(cursor, {duration: 0.3, left: e.pageX, top: e.pageY});
			});
		}

		/* AOS */

		AOS.init({
			easing: "ease-in-out-sine",
			once: true
		});
		

		/* gnb 메뉴 이동 */
		function Trigger(){
			sitemap.classList.remove("active");
			document.body.classList.remove("fixed");
			tab.classList.remove("active");
			gnb.style.display="block";
			gnb.removeAttribute('style');
		}

		function myFn(f){
			topPos=gnbLi[f].firstElementChild.getAttribute("href");
			topPos=pageList[f].offsetTop;

			gsap.to(window, {scrollTo : topPos, duration: 0.4, delay: 0.4});
		}

		for(let i=0; i<gnbLi.length; i++){
			gnbLi[i].addEventListener("click", function(e){
				e.preventDefault();

				myFn(i);
			});
		}

		for(let i=0; i<mGnbLi.length; i++){
			mGnbLi[i].addEventListener("click", function(e){
				e.preventDefault();
				Trigger();
				myFn(i);
			});
		}

		/* tab */
		tab.addEventListener("click", function(e){
			e.preventDefault();
			tab.classList.toggle("active");
			if(sitemap.classList.contains("active")){
				sitemap.classList.remove("active");
				document.body.classList.remove("fixed");
				gnb.style.display="block";
				gnb.removeAttribute('style');
			}
			else{
				sitemap.classList.add("active");
				document.body.classList.add("fixed");
				gnb.style.display="none";
			}
		});


		/* main contact */
		let circle=document.querySelector("svg circle");

		setTimeout(function(){
			circle.classList.add("active");
		}, 3500);

		/* website track */
		let track=document.querySelector(".track");
				let trackText=track.firstElementChild;
			
				let clone=trackText.cloneNode(true);
				track.appendChild(clone);
			
				TweenMax.to(track, 45, {
					x: -1*trackText.clientWidth,
					ease: Linear.easeNone,
					repeat: -1
		});

		return(() => {
			window.removeEventListener("scroll", scrollInteraction);
		});


	});

	let data=props.propsValue;
	return(
			<header id="header">
				<div className="inner">
					<h1><a href="" className="logo">logo</a></h1>
					<Navigation propsValue={data}/>
					<a href="" id="tab">
						<span className="tab_line top"></span>
						<span className="tab_line bottom"></span>
					</a>
				</div>
			</header>
	);
}
export default Header;