function ContentSkillList(props){
	let {strong, cont, num}=props.propsValue;

	return(
		<li>
			<a href="">
				<strong>{strong}</strong>
				<span className="line"></span>
				<span className="cont">{cont}</span>
				<span className="num">{num}</span>
			</a>
		</li>
	);
}

export default ContentSkillList;