import { useSelector } from "react-redux";

function SubMobile(){
	let navigation=useSelector(state => state.navigation);
	return(
		<div id="sitemap">
			<div className="inner">
				<nav id="mobile">
					<ul>
						{ navigation.map((d, i)=> <li key={i}><a href="">{d.toUpperCase()}</a></li>)}
					</ul>
				</nav>
			</div>
		</div>
	);
}

export default SubMobile;