function ContactList(props){
	let data=props.propsValue;

	return(
		<div className="contact" data-aos="fade-down" data-aos-duration="1100" data-aos-delay="900">
			<ul>
				{ data.map((d, i) => <li key={i}><a href={d.href} target="pop">{d.path}</a></li>)
				
				}
			</ul>
		</div>
	);
}

export default ContactList;