import Title from "./Title";
import ContactList from "./ContactList";
import { useSelector } from "react-redux";

function Contact(){
	let title=useSelector(state => state.title);
	let sns=useSelector(state => state.sns);
	return(
		<section id="section4">
			<div className="inner">
				<Title propsValue={title[2]}/>
				<ContactList propsValue={sns}/>
				<div className="copy">
					<span>Copyright ⓒ 2024. JIHEY All rights reserved</span>
				</div>
			</div>
		</section>
	);
}

export default Contact;