function AboutTextBottom(props){
	let data=props.propsValue;
	return(
		<div className="bottom" data-aos="fade-down" data-aos-duration="1100" data-aos-delay="500">
			<p>
				{
					data.map((d, i) => <span key={i}>{d}</span>)
				}
			</p>
		</div>
	);
}

export default AboutTextBottom;