import { Swiper, SwiperSlide } from 'swiper/react';
import { Scrollbar } from 'swiper/modules';
import SubSlideList from './SubSlideList';
import 'swiper/css/scrollbar';
import 'swiper/css';

function SubSlide(props){
	let data=props.propsValue;
	return(
		<div className="open_slider" data-aos="fade-right" data-aos-duration="1000" data-aos-delay="1000">
		<Swiper
			scrollbar={{
				draggable: true,
				hide: false
			}}
			modules={[Scrollbar]}

			slidesPerView={1}
			spaceBetween={40}
			centeredSlides={true}
			breakpoints={{
				600: {	
				slidesPerView: 1,
				initialSlide: 1,			
				},
				797: {
				slidesPerView: 2,
				centeredSlides: false,
				},

				1090: { 
					slidesPerView: 3,
					centeredSlides: false,
				},
			}}
			className="swiper-container subSwiper"
		>
			{
				data.map((d, i) => <SwiperSlide key={i} ><SubSlideList propsValue={d}/></SwiperSlide>)
				
			}
		</Swiper>
	</div>
	);
}

export default SubSlide;