import ContentSkillList from "./ContentSkillList";

function ContentSkill(props){
	let data=props.propsValue;

	return(
		<div className="skill_box">
			<div id="right_move" className="inner">
				<ul>
					{
						data.map((d, i) =><ContentSkillList key={i} propsValue={d}/>)
					}
				</ul>
			</div>
		</div>
	);
}

export default ContentSkill;